    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
      handleResize() {
        this.window.ancho = window.innerWidth;
        this.window.alto = window.innerHeight;
      },
      getVh(){
        var vh = 0;
        if(this.$store.state.banPopUpReconocimientos){
